import React from 'react';
import './Header.css';
import { Link, withRouter } from 'react-router-dom';

class Header extends React.Component {
    constructor() {
        super();
        this.state = {
            userName: '',
            loggedIn: false
        }
    }

    componentDidUpdate() {
        let user = sessionStorage.getItem('userName');

        if(user && !this.state.loggedIn) {
            this.setState({
                userName: user,
                loggedIn: true
            });
        }  
    }

    checkForAdmin = () => {
        if(sessionStorage.getItem('role') === 'Admin') {
            return (
                <li>
                    <Link to="/admin">Admin</Link>
                </li>
            );
        }
    }

    renderSearch = () => {
        const searchQuery = document.getElementsByClassName('search')[0];
        if(searchQuery.value){
            this.props.history.push(`/search?k=${searchQuery.value}&category=aps`);
            searchQuery.value = '';
        }
        else{
            alert("Search Box Should Not Be Empty.")
        }
    }

    renderEnter = (e) => {
        const searchBtn = document.getElementsByClassName('search-btn')[0];
        if(e.which === 13 || e.keyCode === 13) {
            searchBtn.click();
        }
    }

    handleLogout = () => {    
        this.setState({
            userName: '',
            loggedIn: false
        });
        sessionStorage.removeItem('token');
        sessionStorage.removeItem('_rtk');
        sessionStorage.removeItem('userName');
        sessionStorage.removeItem('loggedInEmail');
        sessionStorage.removeItem('asin');
        sessionStorage.removeItem('clogin');
        sessionStorage.removeItem('login');
        sessionStorage.removeItem('role');
        sessionStorage.removeItem('createAdmin');
        this.props.history.push('/signup');   
    }

    CondtionalRender = () => {
        if(this.state.userName === '' || this.state.userName === undefined || this.state.userName === null){
            return(
                <>

                    <li>
                        <button className="glyphicon glyphicon-log-in logout"><Link className="logout" to="/signup">
                            SignUp / SignIn</Link>
                        </button>
                    </li>
                
                </>
            );
        }
        else {
            return(
                <>
                    {/* <li className="welcome-messge">Welcome {sessionStorage.getItem('userName')}</li> */}
                    {this.checkForAdmin()}
                    <li>
                        <button className="glyphicon glyphicon-tasks logout"><Link className="logout" to="/orders">
                            Orders</Link>
                        </button>
                    </li>
                    <li>
                        <button className="glyphicon glyphicon-log-out logout"
                            onClick={this.handleLogout}>
                            Logout
                        </button>
                    </li>

                    <li className="cart">
                        <Link to="/cart"><i className="fa fa-cart-plus"></i> Cart</Link>
                    </li>   
                </>
            ) 
        }

    }
    
    render() { 
        return (
            <header>
                <nav className="navbar navbar-inverse">
                    <div className="container-fluid">
                        <div className="navbar-header">

                            <Link to='/' className="navbar-brand"><b id="brand">amazed</b>.in</Link>
                        </div>

                        <span className="searchbar">
                            <input onKeyPress={this.renderEnter} className="form-group search" type="text" placeholder="Search"/>

                            <button onClick={this.renderSearch} className="btn btn-warning search-btn"><i style={{margin:"0%"}} className="fa fa-search"></i></button>
                        </span>
                            
                        <ul className="nav navbar-nav navbar-right">
                            {this.CondtionalRender()}
                        </ul>
                    </div>
                </nav>
            </header>
        );
    }
}

export default withRouter(Header);